
.contact_component{
    display: grid;
    
    margin-top: 40px;

}
.contact_caja_form{
    justify-self: center;
    align-self: center;
    display: grid;
    background-color: #b0cef9;
    width: 450px;
    padding: 10px 25px;

}

.contact_caja_form h4 {
    font-size: 1.5rem;
    justify-self: center;
    color: #031b4e;

}
.contact_form div{
    margin-bottom: 25px;
    display: grid;
    grid-template-columns: 60px 1fr;
    justify-content: center;
}

.contact_input{
    width: 90%;
    height: 25px;
    

    font-size: 1.1rem;
    border: 0 ;
    
   
    border-bottom: 2px solid #eee;
    
    outline: none;
    padding: 5px;
}

.contact_input:focus{
    border-bottom: 2px solid #003f99;
}
.contact_component_icon{
    justify-self: center;
    align-self: center;
    font-size: 1.2rem;
    width: 25px;
    height: 25px;
    
}



.contact_input_submit_caja{
    display: grid !important;
    grid-template-columns: 1fr !important;

}
.contact_input_submit{
    justify-self: center;
    font-size: 1.3rem;
    background-color: #0061eb;
    color: white;
    border-radius: 8px;
    font-weight: 500;
    display: grid;
    grid-template-columns: 2fr 1fr;
    padding: 4px 10px;
}
.contact_input_submit:hover{
    
    transform: scale(1.1); 

}

.contact_input_text{
    height: 80px;
}

.contact_component_icon_submit{
   width: 20px;
   height: 20px;
   margin: 5px 8px; 
}

@media screen  and (max-width:768px){
    .contact_caja_form{
        justify-self: center;
        align-self: center;
        display: grid;
        background-color: #b0cef9;
        width: 350px;
        padding: 10px 25px;
        
    } 
}
@media screen  and (max-width:450px){
    .contact_caja_form{
        justify-self: center;
        align-self: center;
        display: grid;
        background-color: #b0cef9;
        width: 250px;
        padding: 10px 15px;
        
    } 
}