.project_screen_main{

    margin-top: 90px;
    display: grid;
    justify-content: center;
}

.project_descrip{


    display: grid;
    justify-self: center;
}
.project_descrip h4{
    font-size: 2rem;
    margin-bottom: 0;
}
.project_descrip p{
    font-size: 1.5rem;
    margin-top: 10px;
}

.project_project_list{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(250px,380px));
    padding-left: 5px;
    
}

.project_project_item{
    justify-self: center;
    list-style: none;
    padding: 0 15px;
    background-color: #eee;
}
.project_project_tecno{
    list-style: none;
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 10px;
    padding-left: 0;
}

.project_icon{
    width: 30px;
    height: 30px;
    margin-left: 8px;
}

.project_icon_link{
    width: 40px;
    height: 40px;
    
    margin-bottom: 20px;
}


.project_links{

    display: grid;
    grid-template-columns: 1fr 1fr;
}

.project_link{
    
    display: grid;
    grid-template-columns: 50px 1fr;
    
}
.project_link:hover{
    color: red;
}

.project_link span{
    margin-top: 10px;
}

.project_imagen{
    width: 100%;
    border-radius: 5px;
}

.project_descrip_item h6{
    margin-bottom: 0;
    margin-top: 15px;
    font-size: 1.1rem;
}
