




.skills_component{
    
  
    display: grid;
    justify-content: center;
    margin-top: 30px;
    grid-template-columns: 1fr;
}
.skills_component_lista{

    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 25px;
    margin: 0 auto;
    width: 75vw;
   
    margin-left: 135px;
    
}


.skills_component_titulo h4{
    font-size: 1.8rem;
    margin-top: 0;
    margin-bottom: 10px;
}
.skills_component_titulo p{
    font-size: 1.5rem;
    margin-top: 0;
}


.skills_lista_item {
   
    width: 100%;
    height: 300px;
    border: 1px solid #031b4e;
    box-shadow: 2px 2px 2px 1px #031b4e;
}

.skills_component_icon{

    width: 100%;
    height: 100px;
    margin-top: 15px;
}

.skills_component_titulo{
    font-size: 1.3rem;
    margin-top: 10px;
    margin-left: 15px;
    margin-bottom: 20px;
}



@media screen and (max-width:1300px) {

    .skills_component_lista{
       
        list-style: none;
        
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 25px;
        margin: 0 auto;
        width: 75vw;
        margin-left: 85px;
        
    }

}

@media screen and (max-width:960px) {

    .skills_component_lista{
        list-style: none;
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 25px;
        margin: 0 auto;
        width: 75vw;
        margin-left: 50px;
        
    }

}

@media screen and (max-width:768px) {

    .skills_component{
        display: grid;
      
        justify-content: center;
        
    }    
    .skills_component_lista{
        display: grid;
        grid-template-columns: repeat(9,1fr);
        scroll-snap-type: x mandatory !important;
        overflow-x: scroll !important;
        overflow-y: hidden  !important;
        margin-left: 20px !important;
        
    }    

    .skills_lista_item {
        scroll-snap-align: center !important;
        width: 80vw;
    }    
    
    .skills_component_skill_item{
        font-size: 1.3rem;
    }
}    



