.modal_menu{
    display: none;
    width: 50vw;
    
    
    height: 100vh;
    z-index: 99;
    background-color: #eee;
    
}

.modal_menu_content{
    display: grid;

}


.modal_menu_links{
    list-style: none;
    width: 100%;
    display: grid;
    padding-left: 0;
    justify-content: center;

}
.modal_menu_item{
    display: grid;
    align-items: center;
    margin-top: 10px;
    width: 130px;
    height: 60px;
    background-color: #4550e6;
    border-radius: 12px;
    margin-bottom: 10px;
    padding: 0 5px;

}
.modal_menu_item a{
   
    text-decoration: none;
    justify-self: center;
    align-self: center;

}
.modal_menu_icon{
    width: 25px;
    height: 25px;
    margin-right: 8px;
    color: white;

}
.modal_menu_icon:hover{
    opacity: 0.8;
}
.modal_menu_text{
    font-size: 1.3rem;
    color: white;
    font-weight: 500;
}

.modal_menu_foto{
    width: 100%;
}

.modal_menu_footer{
    align-self: end;
    display: grid;
    grid-template-columns: 1fr 1fr;
    list-style: none;
    width: 100%;
    margin-bottom: 0;
    padding-left: 0;
    background-color:#5d6778;
    justify-items: center;
    
}

.modal_menu_footer li {
    margin: 10px;
}