.main_banner{
    display: grid;
    width: 80%;
    grid-template-columns: 2.5fr 1fr;
    justify-content: center;
    gap: 15px;
    margin: 100px auto;
}




.main_banner_descrip p{

    font-size: 1.5rem;
    
}

.main_banner_descrip button{
    font-size: 1.3rem;
    margin-right: 20px;
    padding: 3px 10px;
    font-weight: bold;
    
}
.main_banner_ver_porta{
    background-color: #0069ff;
    color: white;
    border-radius: 6px;
}

.main_banner_descargar_cv{

    background-color: #4f4f4f;
    color: white;
    border-radius: 6px;
}

.main_banner_ver_porta:hover{
    
    transform: scale(1.1); 
}
.main_banner_descargar_cv:hover{
    transform: scale(1.1); 

}




.main_banner_icon{
    margin: 0 3px;
    margin-right: 5px;
    font-size: 1.1rem;
    padding-top: 5px;
}


.main_banner_titulo h3{
    font-size: 2rem ;
    margin-bottom: 5px;
    font-weight: 800;
}
.main_banner_titulo p{
    font-size: 1.7rem;
    margin-top: 5px;
    font-weight: 500;

    
}



.main_banner_image img{

    width: 100%;
    height: 100%;

}
@media screen and ( max-width:768px){
    .main_banner{
        display: grid;
        
        grid-template-columns: 1fr;
        align-self: center;
        justify-self: center;
        justify-content: center;
        justify-items: center;
        margin-top: 60px;
        
    }

    .main_banner_image{
        display: none;
    }
    .main_banner_icon{
        margin-right: 2px;
        font-size: 0.8rem;
        padding-top: 5px;
    }
}