.footer_component{
    margin-top: 80px;
    display: grid;
    justify-content: center;
   
}

.footer_component_list{
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    
    
}

.footer_icon{
    width: 25px;
    height: 25px;
    
    
}
.footer_component p{
    justify-self: center;
    margin-bottom: 0;
    color: #727aec;

}

.footer_item{
    color: #727aec;
    
}
.footer_item:hover{
    opacity: 0.6;
}

@media screen and (max-width:430px){
    .footer_icon{
        width: 20px;
        height: 20px;
    }

}