.modal {
    display: none;
   
    z-index: 10;
   
}
.modal_window{
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,0.8); /* Black w/ opacity */

}
.modal-content {
    display: grid;
   
  
   
    animation-name: zoom;
    animation-duration: 0.6s;
    justify-self: center;
    align-self: center;
}

.modal_window_content{
    display: grid;
}

@keyframes zoom {
    from {
        transform:scale(0)
    }
    to {
        transform:scale(1)
    }
}