.navbar_lista{

    list-style: none;
    display: grid;
    grid-template-columns: 100px repeat(2,250px);
}

.navbar_link{
    font-size: 1.8rem;
    text-decoration: none;
    color: white;
}
.navbar_link:hover{
    color: red;
}
.navbar_icon_home{
    width: 35px;
    height: 35px;
}
.navbar_icon_link{
    margin-right: 8px;
}

.navbar{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 80px;
    background-color: #080c2d;
    
}
.navbar_icon_menu{
    display: none;
}
.navbar_logo svg{
    color: white;
    width: 40px;
    height: 40px;
}

.navbar_logo :hover{
    color: #4550e6;
}

.navbar_icon_projects{
    width: 18px;
    height: 18px;
    
}

@media screen and (max-width:990px) {
    .navbar_lista{

        list-style: none;
        display: grid;
        grid-template-columns: 80px repeat(2,250px);
    }
    
    .navbar_icon_home{
        width: 30px;
        height: 30px;
    }
    .navbar{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 60px;
        background-color: #080c2d;
        
    }
}

@media screen and (max-width:768px) {


    
    .navbar_icon_menu{
        display: grid;
        color: white;
        height: 25px;
        width: 25px;
    }

    .navbar_lista{
        display: grid;
        grid-template-columns: 40px 1fr;
    }
    .navbar_logo{
        color: white;
        width: 25px;
        height: 25px;
    }
    .navbar_list_item{
        display: none;
    }
    .navbar_list_item_movil{
        justify-self: end;
        margin-right: 25px;
    }
    .main_banner_botones_movil{
        display: grid;
        grid-template-columns: 1.3fr 1fr;
    }
}

